@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Raleway", sans-serif}body{height:100vh;width:100%}nav{background-color:#fff !important}nav .navbar-brand{text-transform:capitalize;font-weight:600;font-size:1.9rem}nav .nav-link{font-weight:500;font-size:1.2em;text-transform:capitalize}nav .nav-link:hover{color:crimson}nav .menu_active{color:crimson;font-weight:bold;border-bottom:2px solid crimson}@media screen and (max-width: 768px){.navbar-brand{font-size:1.5rem !important}}#home{height:80vh;width:100%}#home h1{font-weight:600}#home h1 .brand-name{color:#129cf3}#home h2{font-weight:500;font-size:2.5rem}#home .btn-custom{padding:10px 30px;font-weight:600;border:2px solid crimson;text-transform:uppercase;border-radius:25px;transition:0.5s}#home .btn-custom:hover{background-color:crimson;color:#fff}#home .animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{transform:translateY(10px)}100%{transform:translateY(-10px)}}@keyframes up-down{0%{transform:translateY(10px)}100%{transform:translateY(-10px)}}

